.header {
  position: fixed;
  display: flex;
  width: 100%;
  background-color: black;
  z-index: 50;
  color: #fff;
  align-items: center;
}

.logo {
  font-size: 2rem;
}

.logo > span {
  color: orange;
}

.faBars {
  display: none;
}

nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 70%;
  margin: 20px auto;
  font-size: 1.25rem;
}

.nav-items #hidden {
  display: none;
}

a {
  border-radius: 5px;
}

a:hover {
  cursor: pointer;
  transition: 0.3s ease-in-out;
  text-decoration: underline;
  background-color: orange;
}

@media screen and (max-width: 1070px) {
  ul.nav-items {
    display: none;
  }
  .faBars {
    display: inline-block;
  }
  nav {
    width: 80%;
  }

  ul.nav-items#hidden {
    position: absolute;
    right: 0;
    top: 65px;
    overflow: hidden;
    height: 250px;
    width: 100%;
    background-color: #090a0c;
    color: #fff;
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  ul.nav-items#hidden a:hover {
    border-radius: 5px;
    cursor: pointer;
    background-color: orange;
    color: #fff;
    transition: 0.3s ease-in-out;
    border: none;
  }
}
