.menu-section {
  margin-top: 100px;
  height: 100%;
}

.menu-container {
  margin: 20px auto;
  width: 90%;
}

.breakfast-header,
.dinner-header,
.lunch-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  border: 1px dashed #333;
  width: 100%;
  margin: auto;
}

.breakfast-header h1,
.dinner-header h1,
.lunch-header h1 {
  font-size: 4rem;
  color: #333;
  margin-left: 50px;
}

.menu-faBars {
  margin: 40px;
  font-size: 2rem;
  cursor: pointer;
}

.breakfast-items,
.dinner-items,
.lunch-items {
  border: 1px dashed #333;
  font-size: 1.25rem;
}

.menu-items {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px;
  margin: 10px;
  overflow: hidden;
}

.breakfast-items#hidden,
.dinner-items#hidden,
.lunch-items#hidden {
  display: none;
}

.food-title {
  color: orange;
}

@media screen and (max-width: 780px) {
  .breakfast-items,
  .dinner-items,
  .lunch-items {
    font-size: 1rem;
  }

  .menu-items {
    padding: 10px;
    align-items: flex-start;
  }
}

@media screen and (max-width: 630px) {
  .breakfast-header h1,
  .dinner-header h1,
  .lunch-header h1 {
    font-size: 3rem;
    margin-left: 25px;
  }
  p.item-description {
    width: 60%;
  }
}

@media screen and (max-width: 510px) {
  .breakfast-header h1,
  .dinner-header h1,
  .lunch-header h1 {
    font-size: 2rem;
  }

  .breakfast-items,
  .dinner-items,
  .lunch-items {
    font-size: 0.75rem;
  }

  .menu-faBars {
    font-size: 1.5rem;
  }
}

@media screen and (max-width: 380px) {
  .breakfast-header h1,
  .dinner-header h1,
  .lunch-header h1 {
    font-size: 1.5rem;
    margin-left: 10px;
  }

  .menu-container {
    width: 95%;
  }
}
