@import url(https://fonts.googleapis.com/css2?family=Lobster&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: "Lobster", cursive;
}

.about-container {
  margin: 100px auto 0 auto;
  height: 100%;
}

span {
  color: orange;
}

.about-container > h1 {
  color: #333;
  text-align: center;
}

.about-grid {
  margin-top: 50px;
  display: grid;
  width: 100%;
  grid-template-columns: 40% 60%;
  grid-template-rows: 1fr;
}

.about-grid2 {
  display: grid;
  width: 100%;
  grid-template-columns: 60% 40%;
  grid-template-rows: 1fr;
}

.about-grid3 {
  display: grid;
  width: 100%;
  grid-template-columns: 40% 60%;
  grid-template-rows: 1fr;
}

.about-grid-item {
  height: 500px;
}

.grid1 > p,
.grid4 > p,
.grid5 > p {
  width: 60%;
  margin: 0 auto;
  font-size: 1.4rem;
  text-align: center;
}

.grid2 {
  background-image: url(/static/media/burger1.d668693f.jpg);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  position: relative;
}

.grid3 {
  background-image: url(/static/media/chef1.917a24b4.jpg);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  position: relative;
}

.grid6 {
  background-image: url(/static/media/outside.b1cd87e0.jpg);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  position: relative;
}

.cow-icon {
  font-size: 10rem;
  width: 100%;
  margin: 0 auto;
}

.chef-icon,
.burger-icon {
  font-size: 9rem;
  width: 100%;
  margin: 30px auto;
}

.underline-img {
  width: 100%;
  text-align: center;
  margin: 0 auto;
}

.curly-line {
  margin: -20px auto 20px auto;
  width: 25%;
  height: 100px;
}

@media screen and (max-width: 1340px) {
  .grid1 > p,
  .grid4 > p,
  .grid5 > p {
    width: 80%;
  }
}

@media screen and (max-width: 970px) {
  .grid1 > p,
  .grid4 > p,
  .grid5 > p {
    font-size: 1.2rem;
  }
}

@media screen and (max-width: 760px) {
  .about-grid,
  .about-grid2,
  .about-grid3 {
    display: flex;
    flex-direction: column;
  }

  .about-grid2 {
    flex-direction: column-reverse;
  }
}

@media screen and (max-width: 500px) {
  .grid3 {
    background-position: right;
  }
}

.contact-form-card {
  width: 40%;
  border: 1px solid #333;
  border-radius: 5px;
  background-color: #fff;
  margin: 100px auto;
  text-align: center;
  padding: 20px;
}

.contact-form {
  width: 80%;
  margin: 0 auto;
  text-align: left;
}

input[type="text"],
input[type="email"],
textarea {
  width: 100%;
  padding: 12px;
  border: 1px solid #ccc;
  border-radius: 4px;
  box-sizing: border-box;
  margin-top: 6px;
  margin-bottom: 16px;
}

input[type="submit"] {
  font-size: 1rem;
  background-color: #090a0c;
  color: #fff;
  border: 1px solid white;
  border-radius: 5px;
  cursor: pointer;
  padding: 10px;
}

input[type="submit"]:hover {
  background-color: orange;
  color: #fff;
  transition: 0.3s ease-in;
}

@media screen and (max-width: 965px) {
  .contact-form-card {
    width: 70%;
  }
}

@media screen and (max-width: 750px) {
  .contact-form-card {
    width: 80%;
  }
}

@media screen and (max-width: 480px) {
  .contact-form-card {
    width: 98%;
  }

  .contact-form {
    width: 100%;
  }
}

footer {
  width: 100%;
  background-color: rgb(28, 27, 27);
  color: #fff;

  height: 100%;
}

footer p {
  text-align: center;
  padding: 30px;
}

.home-container {
  height: 950px;
  background-image: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)),
    url(/static/media/hero-background.0b6d6db8.jpg);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  position: relative;
}

.headings-container {
  height: 300px;
  text-align: center;
  position: absolute;
  top: 40%;
  left: 50%;
  transform: translate(-50%, -50%);
}

h1 {
  font-size: 7rem;
  color: #fff;
}

h1 > span,
h2.tagline {
  color: rgb(255, 165, 0);
}

h2.tagline {
  margin-top: 4rem;
  font-size: 1.75rem;
}

h2.tagline > span {
  font-size: 4rem;
}

@media screen and (max-width: 680px) {
  h1 {
    font-size: 5rem;
  }
  h2.tagline {
    font-size: 1.5rem;
  }

  h2.tagline > span {
    font-size: 3rem;
  }
}

@media screen and (max-width: 500px) {
  h1 {
    font-size: 4rem;
  }
  h2.tagline {
    font-size: 1.25rem;
  }

  h2.tagline > span {
    font-size: 2.5rem;
  }
}

@media screen and (max-width: 500px) {
  h1 {
    font-size: 3rem;
  }
  h2.tagline {
    font-size: 1.25rem;
  }

  h2.tagline > span {
    font-size: 2rem;
  }
  .headings-container {
    top: 30%;
  }
}

.menu-section {
  margin-top: 100px;
  height: 100%;
}

.menu-container {
  margin: 20px auto;
  width: 90%;
}

.breakfast-header,
.dinner-header,
.lunch-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  border: 1px dashed #333;
  width: 100%;
  margin: auto;
}

.breakfast-header h1,
.dinner-header h1,
.lunch-header h1 {
  font-size: 4rem;
  color: #333;
  margin-left: 50px;
}

.menu-faBars {
  margin: 40px;
  font-size: 2rem;
  cursor: pointer;
}

.breakfast-items,
.dinner-items,
.lunch-items {
  border: 1px dashed #333;
  font-size: 1.25rem;
}

.menu-items {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px;
  margin: 10px;
  overflow: hidden;
}

.breakfast-items#hidden,
.dinner-items#hidden,
.lunch-items#hidden {
  display: none;
}

.food-title {
  color: orange;
}

@media screen and (max-width: 780px) {
  .breakfast-items,
  .dinner-items,
  .lunch-items {
    font-size: 1rem;
  }

  .menu-items {
    padding: 10px;
    align-items: flex-start;
  }
}

@media screen and (max-width: 630px) {
  .breakfast-header h1,
  .dinner-header h1,
  .lunch-header h1 {
    font-size: 3rem;
    margin-left: 25px;
  }
  p.item-description {
    width: 60%;
  }
}

@media screen and (max-width: 510px) {
  .breakfast-header h1,
  .dinner-header h1,
  .lunch-header h1 {
    font-size: 2rem;
  }

  .breakfast-items,
  .dinner-items,
  .lunch-items {
    font-size: 0.75rem;
  }

  .menu-faBars {
    font-size: 1.5rem;
  }
}

@media screen and (max-width: 380px) {
  .breakfast-header h1,
  .dinner-header h1,
  .lunch-header h1 {
    font-size: 1.5rem;
    margin-left: 10px;
  }

  .menu-container {
    width: 95%;
  }
}

.header {
  position: fixed;
  display: flex;
  width: 100%;
  background-color: black;
  z-index: 50;
  color: #fff;
  align-items: center;
}

.logo {
  font-size: 2rem;
}

.logo > span {
  color: orange;
}

.faBars {
  display: none;
}

nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 70%;
  margin: 20px auto;
  font-size: 1.25rem;
}

.nav-items #hidden {
  display: none;
}

a {
  border-radius: 5px;
}

a:hover {
  cursor: pointer;
  transition: 0.3s ease-in-out;
  text-decoration: underline;
  background-color: orange;
}

@media screen and (max-width: 1070px) {
  ul.nav-items {
    display: none;
  }
  .faBars {
    display: inline-block;
  }
  nav {
    width: 80%;
  }

  ul.nav-items#hidden {
    position: absolute;
    right: 0;
    top: 65px;
    overflow: hidden;
    height: 250px;
    width: 100%;
    background-color: #090a0c;
    color: #fff;
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  ul.nav-items#hidden a:hover {
    border-radius: 5px;
    cursor: pointer;
    background-color: orange;
    color: #fff;
    transition: 0.3s ease-in-out;
    border: none;
  }
}

.hours-container {
  height: 100%;
  background-color: orange;
  margin: -4px auto 0 auto;
  position: relative;
  width: 100%;
}

.hours-container-flex h3 {
  font-size: 3rem;
  padding-bottom: 25px;
}

.hours-container-flex {
  width: 100%;
  padding-top: 50px;
  text-align: center;
  display: flex;
  flex-direction: column;
}

.hours-items {
  font-size: 2rem;
  text-align: justify;
  width: 250px;
  margin: 0 auto 50px auto;
}

