.hours-container {
  height: 100%;
  background-color: orange;
  margin: -4px auto 0 auto;
  position: relative;
  width: 100%;
}

.hours-container-flex h3 {
  font-size: 3rem;
  padding-bottom: 25px;
}

.hours-container-flex {
  width: 100%;
  padding-top: 50px;
  text-align: center;
  display: flex;
  flex-direction: column;
}

.hours-items {
  font-size: 2rem;
  text-align: justify;
  width: 250px;
  margin: 0 auto 50px auto;
}
